import "./Intro.css";
import IntroImg from "../../../assets/gurucool-hero.png";
import Card from "../../UI/Card";

const Intro = () => {
  return (
    <Card className="intro-container">
      <div className="intro">
        <div className="intro-child__1">
          <div className="headings-container">
          <p className="heading heading-1">Join the most advanced classroom on the internet.</p>
          <p className="heading heading-2">Learn. Grow. Thrive</p>
          <p className="heading heading-3">Get all the perks of prestigious schools and universities.</p>
          </div>
          <a href="#learning">Start learning</a>
        </div>
        <div>
          <img className="intro-img" src={IntroImg} alt="intro" />
        </div>
      </div>
    </Card>
  );
};

export default Intro;
