import CommunityImg from "../../../assets/education.png";
import SearchBar from "../../UI/SearchBar";
import SuggestionsContainer from "./SuggestionsContainer";
import "./Welcome.css";
import Card from "../../UI/Card";
import SearchIcon from "../../../assets/search-icon-white.svg";

const Welcome = () => {
  return (
    <Card className="welcome-container">
      <div className="welcome">
        <div>
          <img className="community-img" src={CommunityImg} alt="community" />
        </div>
        <div className="welcome-2nd-child">
          <h2>
            Welcome to your
            <span className="txt-yellow"> smart, fun and personalised </span>
            community.
          </h2>
          <div> 
            <SearchBar imgSrc={SearchIcon} className="search-bar search-bar__welcome" placeholder="Search your interests" />
            <SuggestionsContainer />
          </div>
        </div>
      </div>
    </Card>
  );
};

export default Welcome;
