import Card from "../../UI/Card";
import "./Header.css";


const Header = () => {
  const btnClickHandler = () => {
    document.querySelectorAll(".menu-btn span").forEach((span) => {
      span.classList.toggle("open");
    })
    document.querySelector(".navbar").classList.toggle("open");
    document.querySelector("body").classList.toggle("fixed-position");
}

  if (window.matchMedia("(max-width: 767px)").matches) {
    document.querySelectorAll(".navbar li").forEach((link) => {
      link.addEventListener("click", () => {
        document.querySelector(".navbar").classList.remove("open");
        document.querySelector("body").classList.remove("fixed-position");
        document.querySelectorAll(".menu-btn span").forEach((span) => {
          span.classList.remove("open");
        });
      });
    });
  }

  return (
    <Card className="header-container">
      <header className="header">
        <div className="logo">gurucool</div>
        <button
          onClick={btnClickHandler}
          className="menu-btn"
          data-visible="false"
        >
          <span className="rectangle rectangle-top rectangle-small"></span>
          <span className="rectangle rectangle-middle"></span>
          <span className="rectangle rectangle-bottom rectangle-small"></span>
        </button>
        <nav className="navbar" data-visible="false">
          <ul>
            <li>
              <a className="join-link" href="#join">
                Join now
              </a>
            </li>
            <li>
              <a className="login-link" href="#login">
                Login
              </a>
            </li>
          </ul>
        </nav>
      </header>
    </Card>
  );
};

export default Header;
