import SearchBar from "../../UI/SearchBar";
import Card from "../../UI/Card";
import SearchIcon from "../../../assets/search-icon-black.svg";
import WaveBoat from "../../../assets/waveBottom-boat-mob.svg";
import "./Boat.css";

const Boat = () => {
  return (
    <Card className="boat-container">
      <div className="boat">
        <SearchBar
          imgSrc={SearchIcon}
          className="search-bar boat__search-bar"
          placeholder='"placements"'
        />
        <h3>What are you <span className="txt-yellow">looking for ?</span></h3>
      </div>
      <div className="wave-bg__boat">
        <img alt="wave" src={WaveBoat} />
      </div>
    </Card>
  );
};

export default Boat;
