import ItemsCard from "./ItemsCard";
import Card from "../../UI/Card";
import CampusImg from "../../../assets/campus.svg";
import ConnectImg from "../../../assets/connect.svg";
import LeaderImg from "../../../assets/leader.svg";
import "./Digitise.css";

const Digitise = () => {
  return (
    <Card className="digitise-container">
    <div className="digitise">
      <h3><span className="txt-yellow">DIGITISE</span> YOUR CAMPUS</h3>
      <div className="cards-container">
        <div className="card-1">
          <ItemsCard
            heading="FIND A CAMPUS"
            text="Is your campus already on gurucool?"
            alt=""
            src={CampusImg}
          />
          <button type="search">Search</button>
        </div>
        <div className="card-2">
          <ItemsCard
            heading="CONNECT"
            text="Send a request to us to digitise your campus."
            alt=""
            src={ConnectImg}
          />
          <button>Request</button>
        </div>
        <div className="card-3">
          <ItemsCard
            heading="BE A LEADER"
            text="Join our hand and be a Campus Leader."
            alt=""
            src={LeaderImg}
          />
          <button className="join-btn">Join</button>
        </div>
      </div>
    </div>
    </Card>
  );
};

export default Digitise;
